import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'component',
      component: require('./views/Component.vue').default
    },
    {
      path: '/directive',
      name: 'directive',
      component: require('./views/Directive.vue').default
    }
  ]
})
